.titleText{font-size:20px;font-weight:600;line-height:27.24px;text-align:center;color:#000;position:relative}.titleText::after{content:"";position:absolute;bottom:-2px;left:50%;transform:translateX(-50%);width:50px;border-bottom:2px solid #000}.content{display:flex;flex-direction:column;align-items:center;justify-content:center;gap:30px}.graduateName{font-size:34px;font-weight:600;line-height:36.72px;text-align:center;display:flex;flex-direction:column;gap:5px;flex-direction:column}.graduateDate{font-size:15px;font-weight:300;line-height:18px;text-align:center}.graduateBio{font-size:15px;font-weight:300;line-height:18px;text-align:center}.graduates_Img{border-bottom:8px solid #fb8f8f;border-right:5px solid #fb8f8f;border-left:5px solid #fb8f8f;width:350px;border-radius:15px;height:450px}.mainContent_graduates{display:flex;flex-direction:column;align-items:center;justify-content:center}.graduates{display:grid;grid-template-columns:1fr 1fr 1fr;gap:20px;align-items:flex-start;justify-content:center}.mainContent{display:flex;flex-direction:column;align-items:center;justify-content:center;gap:30px}.nameInfo{display:flex;flex-direction:column;gap:15px;padding:15px 0}.biography{display:flex;flex-direction:column;gap:15px;padding:15px 0}.hr{border-bottom:1px solid #9c9c9c;width:70%;margin:0 auto}